import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class TranslateService {

  private data: any;
  private language: string;

  constructor(
    private http: HttpClient
  ) { }

  public getData(): Promise<any> {
    this.language = this.getLanguaje();
    return new Promise((resolve, reject) => {
      this.http.get('assets/translations/' + this.language + '.json')
        .subscribe(
          data => {
            this.data = data;
            resolve(true);
          },
          error => {
            console.log('Error al recuperar las traducciones: ' , error);
            reject(true);
          }
        );
    });
  }

  public getTranslate(phrase: string): string {
    return this.data[phrase];
  }

  private getLanguaje(): string {
    if ( !Object.is(navigator.language, 'es-ES') ) {
      return 'es-ES';
    } else {
      return navigator.language;
    }
  }

}
