import { BrowserModule } from '@angular/platform-browser';
import { NgModule, APP_INITIALIZER } from '@angular/core';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { AdminComponent } from './admin/admin.component';
import { NavbarLeftComponent } from './navbar-left/navbar-left.component';
import { ContenDerComponent } from './conten-der/conten-der.component';
import { TableComponent } from './table/table.component';
import { LoginService } from './services/login.service';
import { HttpClientModule } from '@angular/common/http';
import { DataService } from './services/data.service';
import { SanitizarPipe } from './pipes/sanitizar.pipe';
import { TranslatePipe } from './pipes/translate.pipe';
import { TranslateService } from './services/translate.service';
import { CapitalizarPipe } from './pipes/capitalizar.pipe';

export function menuFactory(provider: DataService) {
  return () => provider.getData();
}

export function translationsFactory(provider: TranslateService) {
  return () => provider.getData();
}

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    AdminComponent,
    NavbarLeftComponent,
    ContenDerComponent,
    TableComponent,
    SanitizarPipe,
    TranslatePipe,
    CapitalizarPipe
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule
  ],
  providers: [
    LoginService,
    DataService,
    {
      provide: APP_INITIALIZER,
      useFactory: menuFactory,
      deps: [DataService],
      multi: true
    },
    {
      provide: APP_INITIALIZER,
      useFactory: translationsFactory,
      deps: [TranslateService],
      multi: true
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
